@import "../../styles/variables";

.banner {
  display: flex;
  align-items: center;
  position: relative;

  &Image {
    background: center no-repeat url("../../../public/media/avatar_.webp");
    background-size: cover;
    width: 62%;
    padding-top: 48%;
    filter: brightness(0.9);
    opacity: 0.95;
    transition: opacity 0.4s ease;

    &.darkMode {
      opacity: 0.8;
    }
  }

  &Info {
    right: 0;
    top: 50%;
    transform: translateY(-50%);
    position: absolute;
    color: white;
    margin-left: -6.5%;
    mix-blend-mode: difference;

    .backgroundText {
      opacity: 0.9;
      font-family: $fontPlayfair;
      -webkit-font-smoothing: antialiased;
      background-clip: text;
      -webkit-text-fill-color: transparent;
      background-repeat: no-repeat;
      background-size: cover;
      background-position: center;
      background-image: url("../../../public/media/trees.webp");

      h4 {
        font-size: 3.2vw;
        font-weight: 500;
        padding-left: 11vw;
        font-style: italic;
        background-clip: text;
        -webkit-text-fill-color: transparent;
        background-repeat: no-repeat;
        background-size: cover;
        background-position: center;
        background-image: inherit;
      }

      h1 {
        margin-top: -6.5vw;
        font-size: 12vw;
        background-clip: text;
        -webkit-text-fill-color: transparent;
        background-repeat: no-repeat;
        background-size: cover;
        background-position: center;
        background-image: inherit;
      }
    }

    p {
      font-family: $fontLato;
      margin-top: -1rem;
      color: rgba($cyan, 0.7);
      text-transform: uppercase;
      font-weight: 700;
      font-size: 1vw;
      letter-spacing: 7.2px;
      text-align: center;
    }
  }
}

@media (max-width: 480px) {
  .banner {
    padding: 10% 5.5% 0;

    &Image {
      width: 100%;
      padding-top: 77%;
    }

    &Info {
      width: 100%;
      transform: translateY(0);
      top: unset;
      bottom: 10%;
      margin-left: 0;
      text-align: center;

      .backgroundText {
        h1 {
          font-size: 20vw;
          margin-top: -10vw;
        }

        h4 {
          font-size: 5.5vw;
          padding-left: 0;
          margin-left: -7vw;
        }
      }

      p {
        font-size: 3vw;
        letter-spacing: 4px;
        color: $cyan;
      }
    }
  }
}
